body {
    margin: 0;
    padding: 0;
}

header.mdc-top-app-bar--fixed {
    background-color: #365EDC;
}

header .mdc-top-app-bar__row {
    height: 96px;
}

body .mdc-top-app-bar--fixed-adjust {
    padding-top: 96px;
}

main [data-view="banner-wrap"] {
    width: 100%;
    background: linear-gradient(134.95deg, #033A9A 0%, #04115F 100%);
}

[data-view="banner-inner"] {
    height: 583px;
    width: 100%;
    background: url('../images/main-bg.jpg') center top no-repeat;
}

